import store from "@/store";
/**
 * @name props
 */
const STORE = {};

/**
 * @name qiankun
 * @param {Object} props 
 * @description qiankuninitGlobalState
 * @description propsonGlobalStateChange, setGlobalState
 */
const appStore = props => {
  /**
   * @name store
   */
  props?.onGlobalStateChange?.(
    (value, prev) => {
      console.log(`[onGlobalStateChange - ${props.name}]:`, value, prev)
      store.dispatch('appstore/setMsg', value.msg)
    },
    true
  );
  /**
   * @name 
   */
  props?.setGlobalState?.({
    ignore: props.name,
    msg: `${props.name}`,
  });

  /**
   * @name setState
   */
  STORE.setGlobalState = props?.setGlobalState;
  STORE.name = props.name;
};

/**
 * @name setState
 * @param {Object} data  
 */
const setState = (data) => {
  // if (!DataType.isObject(data)) {
  //   throw Error('data');
  // }
  STORE.setGlobalState?.({
    ignore: STORE.name,
    ...data
  })
}

export {
  setState
}
export default appStore;

