import Vue from 'vue';
/*underscore100,: map, filter, invoke 
 ,:, JavaScript,, 
jquery*/
import _ from './underscore.js' ;
/* eslint-disable */
const vue = new Vue();
// GUID  chenlj3
function newGuid() {
  var s = [],
   hexDigits = '0123456789abcdef';
  for (var i = 0; i < 36; i++) {
    s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
  }
  s[14] = '4'; // bits 12-15 of the time_hi_and_version field to 0010
  s[19] = hexDigits.substr(s[19] & 0x3 | 0x8, 1); // bits 6-7 of the clock_seq_hi_and_reserved to 01
  s[8] = s[13] = s[18] = s[23] = '-';
  var uuid = s.join('');
  return uuid;
}
//    datefmt    chenlj3
function nowDate(datefmt) {
  var date = new Date();
  return date.pattern(datefmt || 'yyyy-MM-dd hh:mm:ss');
}
//   chenlj3
function dateTimeFormatter(val, datefmt) {
  if (val == null || val == '') {
  return '';
  }
  var date = new Date(val);
  return date.pattern(datefmt || 'yyyy-MM-dd HH:mm:ss');
}
function getCycleDaysName(value) {
  if (!value) {
    return '';
  }
  let arr = value.split('/'),
   ret1 = _.find(globalJS.cycleDays, item => {
    return item.value == arr[0];
  });
  if (ret1 && ret1.children && ret1.children.length > 0) {
    let ret2 = _.find(ret1.children, item => {
      return item.value == arr[1];
    });
    if (ret2 && ret2.children && ret2.children.length > 0) {
      let ret3 = _.find(ret2.children, item => {
        return item.value == arr[2];
      });
      if (ret3) {
        return ret1.label + '/' + ret2.label + '/' + ret3.label;
      }
        return '';
    }
      return ret1.label + '/' + ret2.label;
  } else if (ret1) {
    return ret1.label;
  }
  return '';
}
//
function messageAlert(title,type, message) {
  vue.$notify({
    'title':title,
    'message': message,
    'type': type
  });
}


/**
 * Date Date String
 * (M)(d)12(h)24(H)(m)(s)(E)(q)  1-2 
 * (y) 1-4 (S) 1 ( 1-3 )
 * eg:
 * (new Date()).pattern("yyyy-MM-dd hh:mm:ss.S") ==> 2006-07-02 08:09:04.423
 * (new Date()).pattern("yyyy-MM-dd E HH:mm:ss") ==> 2009-03-10  20:09:04
 * (new Date()).pattern("yyyy-MM-dd EE hh:mm:ss") ==> 2009-03-10  08:09:04
 * (new Date()).pattern("yyyy-MM-dd EEE hh:mm:ss") ==> 2009-03-10  08:09:04
 * (new Date()).pattern("yyyy-M-d h:m:s.S") ==> 2006-7-2 8:9:4.18
 */
Date.prototype.toMSJSON = function () {
  // eslint-disable-next-line
  var date = '\/Date(' + this.getTime() + ')\/';
  return date;
};

Date.prototype.pattern = function (fmt) {
  if (fmt == undefined) {
    return '';
  }
  var o = {
    'M+': this.getMonth() + 1, //
    'd+': this.getDate(), //
    'h+': this.getHours() / 12 > 1 ? 12 + (this.getHours() % 12 == 0 ? 12 : this.getHours() % 12) : this.getHours() % 12 == 0 ? 12 : this.getHours() % 12, //
    'H+': this.getHours(), //
    'm+': this.getMinutes(), //
    's+': this.getSeconds(), //
    'q+': Math.floor((this.getMonth() + 3) / 3), //
    'S': this.getMilliseconds() //
  },
   week = {
    '0': '\u65e5',
    '1': '\u4e00',
    '2': '\u4e8c',
    '3': '\u4e09',
    '4': '\u56db',
    '5': '\u4e94',
    '6': '\u516d'
  };

  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, String(this.getFullYear()).substr(4 - RegExp.$1.length));
  }
  if (/(E+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (RegExp.$1.length > 1 ? RegExp.$1.length > 2 ? '\u661f\u671f' : '\u5468' : '') + week[String(this.getDay())]);
  }
  for (var k in o) {
    if (new RegExp('(' + k + ')').test(fmt)) {
      fmt = fmt.replace(RegExp.$1, RegExp.$1.length == 1 ? o[k] : ('00' + o[k]).substr(String(o[k]).length));
    }
  }
  return fmt;
};

function tableHeadStyle() {
  return {background:'#FAFAFA',height:'37px'};
}
function tableRowStyle() {
  return {height:'37px'};
}
function getCode() {
  return "PlatForm" + Math.random().toString().substr(3, 2) + S4() + S4();
}
function S4() {
  return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
}
function isJSON(str) {
  if (typeof str === 'string') {
    try {
      let obj = JSON.parse(str)
      if (typeof obj === 'object' && obj) {
        return true
      } else {
        return false
      }
    } catch (e) {
      return false
    }
  }
}
export  default {
  newGuid,// uuid
  nowDate,//  yyyy-MM-dd hh:mm:ss
  dateTimeFormatter,//  yyyy-MM-dd hh:mm:ss
  getCycleDaysName,
  messageAlert,// 
  tableHeadStyle,
  tableRowStyle,
  getCode,
  isJSON
};
