import Vue from "vue";
import VueRouter from "vue-router";
import App from "@/App.vue";
import selfRoutes from "@/router/routes";

/**
 * @name 
 */
import appStore from "./app-store";

const __qiankun__ = window.__POWERED_BY_QIANKUN__;
let router = null;
let instance = null;

/**
 * @name 
 */
const lifeCycle = () => {
  return {
    /**
     * @name 
     * @param {Object} props props
     * @description  bootstrap  mount 
     * @description  unmount 
     */
    async bootstrap() {

    },
    /**
     * @name 
     * @param {Object} props props
     * @description  mount 
     */
    async mount(props) {
      // 
      appStore(props);
      // 
      render(props);
    },
    /**
     * @name /
     */
    async unmount() {
      instance.$destroy?.();
      instance = null;
      router = null;
    },
    /**
     * @name 
     * @param {Object} props props
     * @description  loadMicroApp 
     */
    async update(props) {
      console.log("update props", props);
    }
  };
};

/**
 * @name 
 * @param {Object} props param0 qiankunprops
 * @description {Array} router 
 * @description {String}  
 */
console.log(selfRoutes)
const render = ({ routerBase, container } = {}) => {
  Vue.config.productionTip = false;
  router = new VueRouter({
    base: __qiankun__ ? routerBase : "/",
    mode: "history",
    routes: selfRoutes
  });
  instance = new Vue({
    router,
    render: h => h(App)
  }).$mount(container ? container.querySelector("#app") : "#app");
};

export { lifeCycle, render };
