import axios from 'axios';
import store from '../store';
//start
import {ApiRequestError} from './api-response';
import {Loading, Message} from 'element-ui'
import {ApiResultCode} from './api-response'
import {getUrlParam, getQueryString,isAppViewOrLogin} from '@/utils/js/url_path_utils';
import {LocalStorageUtil} from '@/utils/js/localstorage_utils';
import MICRO_CONFIG from './platform_config';
import JSEncrypt from 'jsencrypt';
import common from "@/utils/js/common";
import bus from "@/utils/js/bus";
//end
axios.defaults.withCredentials = true;
var loadingInstance
//start
axios.interceptors.request.use(config => {
  debugger
  //preview
  addPreview(config)
  // subGuidSubSystemName
  addSubGuidAndSubSystemName(config)
  //loading
  addLoading(config)
  let t = getUrlParam('token');
  let timestampTokenRes;
  if (MICRO_CONFIG.timestampToken) {
    let date = Date.now();
    const securityValue = "GisqSecurity_" + common.newGuid() + "_" + date;
    let pubKey = window.localStorage.getItem("gisqPublic") as string;
    let options = {};
    let encryptStr = new JSEncrypt(options);
    encryptStr.setPublicKey(pubKey);
    timestampTokenRes = encryptStr.encrypt(securityValue);
  }

  //bitokenbitoken
  let token = window.localStorage.getItem('X-Gisq-Token');
  if (isAppViewOrLogin()) {
    token = LocalStorageUtil.getItem('X-Gisq-Token');
  }

  if (MICRO_CONFIG.timestampToken) {
    config.headers['X-Gisq-Token'] = token + "." + timestampTokenRes;
  } else {
    config.headers['X-Gisq-Token'] = token;
  }
  //config.headers['X-Gisq-Token']   config.url +=
  //config.url += '?access_token=' + token?.substr('bearer '.length);

  config.cancelToken = new axios.CancelToken(function (cancel) {
    store.commit('pushToken', {cancelToken: cancel})
  })

  return config;
}, error => {
  return Promise.reject(error);
});

axios.interceptors.response.use(res => {
    if (res.data.code === 702) {
      localStorage.removeItem('access-user');
      window.location.href = MICRO_CONFIG.routePath + '/#/login';
      // (this as any).$router.push()
    } else if (res.data.user) {
      localStorage.setItem('access-user', res.data.user);
    }
    if (res) {
      //
      if (res.request.responseURL.indexOf('gisqForm') >= 0) {
        // 
        if (res.request.responseURL.indexOf('checkImportFormExist') >= 0 || res.request.responseURL.indexOf('exportForm') >= 0) {
          return res
        } else {
          if (res.data && res.data.status && res.data.code && res.data.status != 200 && res.data.code != 200) {
            loadingInstance?.close();
            Message({
              message: res.data.msg ? res.data.msg : "",
              type: 'error',
              duration: 3 * 1000
            });
          }
        }
        loadingInstance?.close();
        return res;
      } else {
        if (res.data instanceof Blob) {
          if (res.status != 200) {
            //loadingloading
            loadingInstance?.close();
            Message({
              message: "",
              type: 'error',
              duration: 3 * 1000
            });
            throw new ApiRequestError(ApiResultCode.error, "");
          }
          loadingInstance?.close();
          return res;
        }
        //
        if (res.data && res.data.status && res.data.status != 200 && res.data.code != 200) {
          //BI
          if (res.request.responseURL.indexOf('gisqBI') >= 0) {
            res.data.msg = 'BI' + res.data.msg;
            loadingInstance?.close();
          }
          loadingInstance?.close();
          //message
          if (res?.data?.status == 900) {
            if(res?.data?.msg == ''){
              localStorage.clear(); //
              window.location.href = MICRO_CONFIG.routePath + '/#/login';
            }
            Message({
              message: res.data.msg ? res.data.msg : "",
              type: 'warning',
              duration: 3 * 1000
            });
          } else {
            //message
            Message({
              message: res.data.msg ? res.data.msg : "",
              type: 'error',
              duration: 3 * 1000
            });
          }
          if (res.data.msg) {
            throw new ApiRequestError(res.data.status, res.data.msg);
          }
        }
        loadingInstance?.close();
        return res;
      }
    }
    loadingInstance?.close();
    checkCode(res);
    return res
  },
  //
  error => {
    // todo: message code
    if (axios.isCancel(error)) { // Promise
      return new Promise(() => {
      });
    } else {
      loadingInstance?.close();
      const response = error.response
      checkStatus(response)
    }
  });


//
function checkStatus(response) {
  if (response && response.status != 200) {
    if (response.status == 401) {
      handleTokenLost()
      // localStorage.removeItem("X-Gisq-Token");
      // localStorage.clear(); //
      //401title
      document.title = MICRO_CONFIG.title;
      if (window.parent != window) return
    } else {
      return Promise.reject(response.data);
      /*Message({
        //message:err.response.data.errMsg,
        message: '',
        type: 'error',
        duration: 5 * 1000
      });
      throw Error('');*/
    }

  }
}

//
function checkCode(res) {
  if (res.data.status != "200" && res.data.status != "500") {
    Message({
      message: res.data.msg,
      type: 'error',
      duration: 3 * 1000
    });
    throw Error(res.data.msg);
  } else {
    return res
  }

}

//loading
function addLoading(config) {
  const loadingUrl = ["biz/definition/importModels"]
  let isLoading = loadingUrl.some(function (item) {
    return config.url.indexOf(item) > -1;
  });
  if (isLoading) {
    loadingInstance = Loading.service({
      lock: true,
      text: '',
      spinner: 'el-icon-loading',
      background: 'rgba(0, 0, 0, 0.7)'
    });
  }
}
// subGuidSubSystemName
function addSubGuidAndSubSystemName(config){
  const subGuidUrl = getUrlParam('subGuid');
  let subGuid;
  if (subGuidUrl) {
    subGuid = subGuidUrl;
  } else {
    subGuid = window.localStorage.getItem('subGuid');
  }
  const notAddSubGuidUrl=["auth/oauth/login"]
  let  notAddSubGuid = notAddSubGuidUrl.some(function (item) {
    return config?.url.indexOf(item) > -1;
  });
  if (subGuid && !notAddSubGuid) {
    //subGuid subGuid
    if(!getQueryString("subGuid", config.url)){
      if (config['params']) {
        config.params['subGuid'] = subGuid;
      } else {
        config['params'] = {'subGuid': subGuid};
      }
     }
    //
    const exitNameArr = (!localStorage.appNameArr) ? JSON.parse("[]") : JSON.parse(localStorage.appNameArr);
    if (exitNameArr) {
      const index = exitNameArr.findIndex(value => {
        return value.key == subGuid
      });
      if (exitNameArr[index] && exitNameArr[index].name) {
        if (config['params']) {
          config.params['subSystemName'] = exitNameArr[index].name;
        } else {
          config['params'] = {'subSystemName': exitNameArr[index].name};
        }
      }
    }
  }
}
function addPreview(config){
  //preview 
  const notAddPreviewUrl=["logmgr/getSecureAuditLog"]
  let  notAddPreview = notAddPreviewUrl.some(function (item) {
    return config?.url.indexOf(item) > -1;
  });
  if(!notAddPreview){
    if (config['params']) {
      config.params['preview'] = true;
    } else {
      config['params'] = {'preview': true};
    }
  }
}
//401tokentokenLost
function handleTokenLost(){
  if (isAppViewOrLogin()) {
    // 401
    const token=localStorage.getItem("X-Gisq-Token");
    const appToken=LocalStorageUtil.getItem("X-Gisq-Token")
    LocalStorageUtil.setItem("tokenLost",true);
    if(token===appToken){
      localStorage.setItem("tokenLost","true");
    }
  }else{
    const token=localStorage.getItem("X-Gisq-Token");
    if(token){
      localStorage.setItem("tokenLost","true");
      //window.onstoragelocalStorage -tab,
      bus.$emit("showLoginForm",{isDialog:true})
      //appObjecttokenLost
      setAppObjectTokenLost()
    }else {
      window.location.href = MICRO_CONFIG.routePath + '/#/login';
    }
  }
}
function setAppObjectTokenLost() {
    try {
      const token=localStorage.getItem("X-Gisq-Token")
      let appObjectStr = localStorage.getItem("appObject");
      if (appObjectStr) {
        //json
        let appObject = JSON.parse(appObjectStr);
        // appObject token tokenLosttrue
        for(let key in appObject){
          let equalToken=appObject[key]["X-Gisq-Token"]===token
          if(equalToken){
            appObject[key].tokenLost=true
          }
        }
        localStorage.setItem('appObject', JSON.stringify(appObject));
      }
    } catch (error) {
      console.log("appObjecttokenLost");
    }
}
export default axios;
